import React, { Component } from 'react';
import axios from 'axios';
import moment from 'moment';

class App extends Component {
  constructor(){
    super();
    const data = localStorage.getItem('dataUpdated')
    this.state = {
      base: data ? JSON.parse(data).base : 1,
      valor: data ? JSON.parse(data).valor : 0,
      dataUpdated: data ? new Date(JSON.parse(data).dataUpdated) : new Date(),
      quantidade: 1,
      inverter: false
    }
  }

  saveLocal(updatedData){
    localStorage.setItem('dataUpdated', JSON.stringify(updatedData))
  }

  updateValues({ timestamp, rates, base }){
    console.log(timestamp, rates, base)
    const updatedData = {
      valor: rates.BRL,
      base: rates.USD,
      dataUpdated: new Date(Number(timestamp+'000'))
    };
    this.saveLocal(updatedData)
    this.setState(updatedData);
  }

  componentDidMount(){
    axios.get('http://data.fixer.io/api/latest?access_key=545b0e1b3d09b5719c3cdd420985a85a&symbols=BRL,USD')
    .then(({data}) => this.updateValues(data) ).catch(e => console.warn(e))
  }

  render() {
    const { base, valor, dataUpdated, quantidade, inverter } = this.state;
    return (
      <div className="App">

        <div className="flex-center">
          <h2 className="text-center bold">{inverter ? 'Real em Dolár' : 'Dolár em Real'}</h2>
          &nbsp;
          <a className="inverter text-center" onClick={() => this.setState({ inverter: !inverter })}>Inverter</a>
        </div>

        <div className="form-group">
          <label>{ !inverter ? 'Dolár: ' : 'Reais: ' }</label>
          <input className="form-control" value={quantidade} onChange={(ev) => this.setState({quantidade: Number(ev.target.value)})} />
        </div>

        <div className="form-group">
          <label>{ inverter ? 'Dolár: ' : 'Reais: ' }</label>
          <span>{ !inverter ? ((valor/base)*(quantidade)).toFixed(2) : ((base/valor)*(quantidade)).toFixed(2) }</span>
        </div>
        
        <br />

        <h5>Atualizado em: {moment(dataUpdated).format('DD/MM/YYYY HH:mm')}</h5>
      </div>
    );
  }
}

export default App;



